Object.defineProperty(HTMLMediaElement.prototype, 'playing', {
  get: function () {
      return !!(this.currentTime > 0 && !this.paused && !this.ended && this.readyState > 2);
  }
});

$(document).ready(function () {
  const videoElement = document.getElementById('main-animation');

  if(videoElement){
    $('body').on('click touchstart', function () {
      if (videoElement.playing) {
          // video is already playing so do nothing
      } else {
          // video is not playing
          // so play video now
          videoElement.play();
      }
    });
  }

  if($('.video-page').length > 0){
    $('.video__preview-icon').on('click', function () {
      const iconPlay = $(this)
      const video = $(this).parent().find('.video-page')[0].contentWindow
      iconPlay.fadeOut(300,function(){
        video.postMessage('{"event": "command", "func": "playVideo", "args": ""}', "*");
        iconPlay.parent().find('.video__preview-image img').fadeOut()
      })
    })
  };




  $('#brief-form').submit(function(event){
    const direction = window.location.search.slice(1).split('=')[1] || 'нет';
    $('input[name="direction"]').val(decodeURI(direction))
    event.preventDefault()
    const dataForm = $(this).serialize()  
    const form = $(this)
    const btnSubmit = $('#brief-form-submit')
    
    
    btnSubmit.addClass('disabled').text('Отправка...')
    $('.brief__form-body').addClass('active')
    $('.brief__form__answer').fadeIn()
    $.ajax({
      type: "POST", //Метод отправки
      url: form.attr('action'), //путь до php фаила отправителя
      data: dataForm,
      success: function() {
        $('.brief__form__answer-loading').fadeOut(200)
        $('.brief__form__answer-success').delay(400).fadeIn()
      },
      beforeSend : function () {
        $('.brief__form__answer-loading').fadeIn()
      },
      error: function(jqxhr, status, errorMsg) {
        console.log(errorMsg);
        $('.brief__form__answer-loading').fadeOut(200)
        $('.brief__form__answer-error').delay(400).fadeIn()
      },
      complete: function () {
        setTimeout(() => {
          form.trigger('reset');
          btnSubmit.removeClass('disabled').text('Отправить')
          $('.brief__form-body').removeClass('active')
          $('.brief__form__answer').fadeOut()
          $('.brief__form__answer-loading').delay(400).hide()
          $('.brief__form__answer-error').delay(400).hide()
          $('.brief__form__answer-success').delay(400).hide()
        }, 5000);
      }
    })
  })

  jQuery(function($){
    if($("#input-tel").length){
      $("#input-tel").mask("+7 (999) 99-99-999");
    }
  });

  $("body").animate({ opacity: 1 });
  let isOpen = false
  $('#header-menu').on('click', function(e){
    e.preventDefault()
    $(this).toggleClass('active')
    $('.header__navigation-container').fadeToggle().css('display', 'flex')
    if(!isOpen){
      $('body').css('overflow','hidden')
    } else {
      $('body').css('overflow','inherit')
    }
    $(this).css('pointer-events', 'none')
    setTimeout(() => {
      $(this).css('pointer-events', 'inherit')
    }, 300);
    isOpen = !isOpen
  })

  if($('[data-progress]').length !== 0){
      const blockTop = $('.business-impact__list').offset().top;
      let CountUpFlag = 0;
      const $window = $(window);
      $window.on('load scroll', function() {
          const top = $window.scrollTop();
          const height = $window.height();
          if (top + height >= blockTop && CountUpFlag == 0) {
              CountUp();
              CountUpFlag = 1;
          }
      });

      function CountUp() {
          $('[data-progress]').each((index,item)=>{
            const width = $(item).attr('data-progress')
            $(item).append('<span></span>')
            if(width > 85){
              $(item).children('span').css('background', '#1157FF')
            }
            $(item).children('span').animate({
              width: width + '%'
            },1000)
          });
      }
  }

  $('.scroll-btn').on('click', function (e) {
    const section = $(this).attr('href') 
    $('html, body').animate({
      scrollTop: $(section).offset().top
    }, 500)
  })
  
})